.cv__navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  background-color: color(srgb-linear 0.17 0.25 0.54 / 1);
  box-shadow: rgba(105, 8, 8, 0.15) 0px 5px 15px 0px;
  padding: 0 2rem; /* Padding applied evenly on left and right */
}

.cv__navbar-heading {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.cv__navbar-heading_links {
  cursor: pointer;
  text-decoration: none; /* Remove underline from Link */
}

.cv__navbar-heading_links h1 {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 25px;
  color: white; /* Set the color of h1 text */
}

.inline {
  display: inline;
}

.cv__navbar-links {
  list-style: none;
  display: flex;
  align-items: center;
  margin-right: 3rem;
}

.cv__navbar-links li {
  color: white;
  font-size: 16px; /* Adjust font size as needed */
  margin-right: 2rem; /* Adjust spacing between links */
  cursor: pointer;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: bold;
  letter-spacing: 0.02cm;
  padding: 30px 0;
  /* min-height: 80px; */
}

.cv__navbar-links li a {
  color: white; /* Set link color */
  text-decoration: none; /* Remove underline from links */
  transition: color 0.3s ease-in-out;
}

.cv__navbar-links li a:hover {
  color: rgb(0, 0, 0); /* Change color on hover */
}

.dropdown {
  position: relative;
}

.dropdown-content li {
  text-align: left;
}

.dropdown-div {
  display: flex;
  position: absolute;
  /* background-color: color(srgb-linear 0.17 0.0 0.6 / 0.6); */
  min-width: 100%;
  /* box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2); */
  z-index: 5;
  /* transform: translateY(-2px); */
  transition: opacity 2s ease-in-out, transform 2s ease-in-out;
  padding-top: 30px;
  align-items: center;
  justify-content: center;
}

.dropdown-content {
  /* margin-top: 20px; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  background-color:aliceblue;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dropdown-content li {
  color: black;
  display: flex;
  align-items: flex-end;
  padding: 5px 0;
  text-decoration: none;
  list-style: none;
  white-space: nowrap;
  transition: all 0.5s ease;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 200;
}

.dropdown-content li a {
  color: rgb(5, 5, 5);
}

.plus {
  margin-right: 10px;
}

.dropdown-content li:hover {
  transform: scale(1.1);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.cv__navbar-smallscreen {
  display: none;
}

.cv__navbar-smallscreen_menu {
  z-index: 6;
}

.cv__navbar-smallscreen_overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  transition: 0.9s 0.5s ease, transform 0.5s ease;
  flex-direction: column;
  z-index: 1;
}

.cv__navbar-smallscreen_links {
  margin-top: 23%;
  display: flex;
  flex-direction: column;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.cv__navbar-smallscreen_links li {
  color: white;
  font-size: 18px;
  margin: 1.2rem 1rem;
  cursor: pointer;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 180;
  letter-spacing: 0.1cm;
}

.cv__navbar-smallscreen_links li a {
  color: white; /* Set link color */
  text-decoration: none; /* Remove underline from links */
  transition: color 0.5s ease-in-out;
}

.cv__navbar-smallscreen_links li a:hover {
  color: lightgrey; /* Change color on hover */
}

@media screen and (max-width: 800px) {
  .cv__navbar-links {
    display: none;
  }

  .cv__navbar-smallscreen {
    margin-right: 4rem;
    display: flex;
  }
}

@media screen and (max-width: 550px) {
  .cv__navbar-heading_links h1 {
    font-size: 23px;
  }
}

@media screen and (max-width: 465px) {
  .cv__navbar-heading_links {
    flex-direction: column;
    align-items: flex-start;
  }

  .cv__navbar-heading_square {
    align-self: flex-start;
    margin-top: 3%;
  }
}

@media screen and (max-width: 335px) {
  .cv__navbar-heading_links h1 {
    font-size: 22px;
  }
}

@media screen and (max-width: 300px) {
  .cv__navbar-heading_links h1 {
    font-size: 19px;
  }
}
